import {db} from 'services/firebase';

 const  goToUrl = async () => {
	const url = window.location.pathname.split('/')[1];
	if (url !== '') {
		const data =  await db.collection("urls").doc(url).get()
		if (data.data() !== undefined){
			window.location.href = data.data().url;
		}

	}
	return true
};
export default goToUrl;
